@use "../../assets/SASS/variables" as *;



@media (max-width: 480px) {
  .nav__input {
    color: grey;
    cursor: pointer;
    -webkit-transition: color 1s;
    transition: color 1s;
    font-size: 20px;
    margin-left: 17px;

  }
  
  .nav__input:hover {
    color: black;
    text-decoration: underline;
  }
  .nav__input{
    font-family: $font-header-solid;
    font-size: 25px;
    font-weight: 900;
  }
  
  }

  @media (min-width: 480px) {
    .nav__input {
      display: none;
      color: grey;
      cursor: pointer;
      -webkit-transition: color 1s;
      transition: color 1s;
      font-size: 20px;
      margin-left: 17px;
      
    }
    
    .nav__input:hover {
      color: black;
      text-decoration: underline;
    }
    .nav__input{
    
      font-family: $font-header-solid;
      font-size: 25px;
      font-weight: 900;
    }
    
    }